import React, { useEffect, useRef, useState } from 'react'

import bg from '../../../../../images/case/card.png'
import itemImage from '../../../../../images/case/case-image.png';

import CaseCard from './CaseCard';
import CustomSwiper from '../../../../../common/CustomSwiper/CustomSwiper';
import animateSideTextParallax from '../../../../../services/animateSideTextParallax';
import Button from './../../../../../common/Button';
import SideText from './../../../../../common/SideText';
import CustomParticles from './../../../../../common/CustomParticles/CustomParticles';
import CaseList from './CaseList';

let gallery = [
    {
        title: 'Vifloor',
        text: 'Web App Development',
        href: '#',
        bg: bg,
        id: 1,
        texts: ['Custom WordPress Visualizer using latest transformations Lasy load and deferred assets', 'Thinkbound Inc. implements impressive technical expertise and a thoughtful problem-solving mindset.'],
        image: itemImage
    },
    {
        title: 'Vifloor',
        text: 'Web App Development',
        href: '#',
        bg: bg,
        id: 2,
        texts: ['Custom WordPress Visualizer using latestd deferred assets', 'Thinkbound Inc. implements impressive technical exem-solving mindset.'],
        image: itemImage
    },
    {
        title: 'Vifloor',
        text: 'Web App Development',
        href: '#',
        bg: bg,
        id: 3,
        texts: ['Custom WordPress Visualizer using latest', 'Thinkbound Inc. implements impressivelem-solving mindset.'],
        image: itemImage
    },
    {
        title: 'Vifloor',
        text: 'Web App Development',
        href: '#',
        bg: bg,
        id: 4,
        texts: ['Custom WordPress Visualizer using latest', 'Thinkbound Inc. WordPress implements impressivelem-solving mindset.'],
        image: itemImage
    },
]

export default function Case() {
    let caseWrapperRef = useRef(null);
    let sideTextRef = useRef(null);

    const [itemIndex, setItemIndex] = useState(0);

    useEffect(() => {
        let caseWrapperElement = caseWrapperRef.current;
        let sideTextElement = sideTextRef.current;
     
        animateSideTextParallax(caseWrapperElement, sideTextElement);
    }, [])

    return (
        <section className="section case" ref={caseWrapperRef}>
            <div className="title case__title container">
                <div className="title__cat">Case Studies</div>
                <h1>Recent Breakthroughs</h1>
            </div>
            <CustomParticles modificator="case" />
            <div className="case__wrapper container">
                <SideText text='case studies' modificator="case" sideTextRef={sideTextRef} />
                <div className="case__column">
                    <CaseList itemIndex={itemIndex} gallery={gallery} />
                    <Button type='link' className='case__button' text='View Case study' textClass='button__text--blue' href='#' />
                </div>
                <CustomSwiper gallery={gallery} Component={CaseCard} id="case" setItemIndex={setItemIndex} />
            </div>
        </section>

    )
}
