import React from 'react'
import Star from '../../../../../common/icons/Star';

export default function TestimonialsCard({ item }) {
    return (
        <div className="card card--testimonials">
            <img src={item.image} className="card__image" alt=""/>
            <p className="card__text">{item.text}</p>
            <ul className="card__rating">
                <li className="card__rating-item"><Star className="card__rating-icon"/></li>
                <li className="card__rating-item"><Star className="card__rating-icon"/></li>
                <li className="card__rating-item"><Star className="card__rating-icon"/></li>
                <li className="card__rating-item"><Star className="card__rating-icon"/></li>
                <li className="card__rating-item"><Star className="card__rating-icon"/></li>
            </ul>
            <h2 className="card__title">{item.author}</h2>
        </div>
    )
}
