import './fonts/stylesheet.css';
import './sass/style.scss';

import { Route, Switch, useLocation } from 'react-router-dom';

import Header from './modules/header/components/Header';

import { useState } from 'react';
import MainPage from './modules/main-page/components/MainPage';
import Footer from './modules/footer/components/Footer';
import Blog from './modules/blog/components/Blog';
import { AnimatePresence } from 'framer-motion';
import Blogs from './modules/blogs/components/Blogs';
import Service from './modules/service/components/Service';

function App() {
    const location = useLocation();
    const [mobileMenuShow, setMobileMenuShow] = useState(false);
    const [linkProps, setLinkProps] = useState({});

    if (mobileMenuShow) {
        document.body.style.overflow = 'hidden';
    } else {
        document.body.style.overflow = 'auto';
    }

    function handleMobileMenuShow() {
        setMobileMenuShow((mobileShowState) => {
            return !mobileShowState;
        });
    }

    return (
        <>
            <Header mobileMenuShow={mobileMenuShow} handleMobileMenuShow={handleMobileMenuShow} />
            <AnimatePresence initial={false} exitBeforeEnter>
                <Switch location={location} key={location.pathname}>
                    <Route path="/" exact component={MainPage} />
                    <Route path="/blog/:id">
                        <Blog linkProps={linkProps} setLinkProps={setLinkProps} />
                    </Route>
                    <Route path="/blogs">
                        <Blogs setLinkProps={setLinkProps} />
                    </Route>
                    <Route path="/service" component={Service} />
                </Switch>
            </AnimatePresence>
            <Footer />
            <div className={"background" + (mobileMenuShow ? " active" : "")} onClick={handleMobileMenuShow} />
        </>
    );
}

export default App;
