import React, { useEffect, useRef } from 'react'

import SideText from '../../../../../common/SideText'
import image from '../../../../../images/testimonials/testimonials-card.png'
import thinkboundIcon from '../../../../../icons/thinkbound.png'
import clutchIcon from '../../../../../icons/clutch.png'

import CustomSwiper from '../../../../../common/CustomSwiper/CustomSwiper';
import animateSideTextParallax from '../../../../../services/animateSideTextParallax';
import Button from '../../../../../common/Button';
import TestimonialsCard from './TestimonialsCard';

let gallery = [
    { author: 'Box Me Up Inc.', text: '"Thinkbound checks all of our boxes and provides everything we request."', id: 1, image },
    { author: 'Storage Protectors', text: '"What makes Thinkbound stand out is their ability to create solutions that dont otherwise exist."', id: 2, image },
    { author: 'Box Me Up Inc.', text: '"Thinkbound checks all of our boxes and provides everything we request."', id: 3, image },
    { author: 'Storage Protectors', text: '"What makes Thinkbound stand out is their ability to create solutions that dont otherwise exist."', id: 4, image },
    { author: 'Box Me Up Inc.', text: '"Thinkbound checks all of our boxes and provides everything we request."', id: 5, image },
    { author: 'Storage Protectors', text: '"What makes Thinkbound stand out is their ability to create solutions that dont otherwise exist."', id: 6, image },
]

export default function Testimonials() {

    let caseWrapperRef = useRef(null);
    let sideTextRef = useRef(null);

    useEffect(() => {
        let caseWrapperElement = caseWrapperRef.current;
        let sideTextElement = sideTextRef.current;

        animateSideTextParallax(caseWrapperElement, sideTextElement);
    }, [])

    let options = {
        initialSlide: 1,
        centeredSlides: true,
        spaceBetween: 0,
        effect: 'coverflow',
        coverflowEffect: {
            rotate: 20,
            stretch: 124,
            depth: 250, 
            modifier: 1,
            slideShadows: false,
        },
        loop: true,
        breakpoints: {
            989: {
                coverflowEffect: {
                    stretch: 103,
                },
                slidesPerView: 2,
            },
            767: {
                coverflowEffect: {
                    stretch: 71,
                },
                slidesPerView: 2,
            },
            319: {
                coverflowEffect: {
                    stretch: -100,
                },
                slidesPerView: 1,
            },
        }
    }

    return (
        <section className="section testimonials" ref={caseWrapperRef}>
            <div className="testimonials__wrapper container">
                <div className="title title--center">
                    <div className="title__cat">Phone conducted interviews</div>
                    <h1>Client Feedback</h1>
                </div>
                <SideText text='testimonials' modificator="testimonials" sideTextRef={sideTextRef} />
                <CustomSwiper gallery={gallery} Component={TestimonialsCard} id="testimonials" options={options} />
                <div className="testimonials__buttons">
                    <Button type='link' className='testimonials__button' text='View in Clutch' textClass='button__text--blue' href='#' icon={clutchIcon} />
                    <Button type='link' className='testimonials__button' text='Start a Project' textClass='button__text--blue' href='#' icon={thinkboundIcon} />
                </div>
            </div>
        </section>

    )
}
